import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAuth } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { LanguageService } from 'src/app/cafe/services/language.service';

@Component({
  selector: 'app-ingredient',
  templateUrl: './ingredient.component.html',
  styleUrls: ['./ingredient.component.scss']
})
export class IngredientComponent implements OnInit {

  constructor(
    private router: Router,
    private vhAuth: VhAuth,
    private vhComponent: VhComponent,
    private languageService: LanguageService
  ) { }

  ngOnInit(): void {
  }
  gotoCategory() {
    if (this.vhAuth.checkMyPermission('ingredient_enable_manage_category')) {
      this.router.navigate(['/cafe/dashboard/management/ingredient/category']);
    } else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  gotoIngredientList() {
    if (this.vhAuth.checkMyPermission('ingredient_enable_manage_ingredient')) {
      this.router.navigate(['/cafe/dashboard/management/ingredient/list']);
    } else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  gotoPrintBarcodeQick(){
    this.router.navigate(['/cafe/dashboard/management/ingredient/print-barcode']);
  }
  
}
